  .single-podcast p,
  .single-podcast li {
    font-size: 18px;
    line-height: 27px; }
  .single-podcast #wrapper {
    overflow: hidden;
    display: flex;
    flex-direction: column;
    align-items: stretch; }
    .single-podcast #wrapper #header-top {
      display: none; }
    .single-podcast #wrapper main {
      display: flex;
      align-items: stretch;
      justify-content: center;
      min-height: 100%; }
    .single-podcast #wrapper #menu-primary,
    .single-podcast #wrapper #menu-button {
      display: none; }
    .single-podcast #wrapper footer {
      display: none; }
    .single-podcast #wrapper #podcast {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      align-items: stretch;
      background: #f6f8fa; }
      .single-podcast #wrapper #podcast .intro {
        display: flex;
        align-items: center; }
        .single-podcast #wrapper #podcast .intro .logo {
          max-height: 80px; }
        .single-podcast #wrapper #podcast .intro .container {
          display: flex;
          flex-wrap: wrap;
          gap: 80px; }
          @media (max-width: 768px) {
            .single-podcast #wrapper #podcast .intro .container {
              flex-direction: column;
              margin-top: 40px; } }
          .single-podcast #wrapper #podcast .intro .container h2 {
            line-height: 3rem;
            margin-bottom: 2rem; }
          .single-podcast #wrapper #podcast .intro .container section {
            display: flex;
            flex: 1;
            flex-direction: column; }
            .single-podcast #wrapper #podcast .intro .container section .home-hero-title {
              color: #664fb7;
              letter-spacing: 3px;
              text-transform: uppercase;
              margin: 30px 0px;
              font-family: "Avenir-Heavy", sans-serif; }
            .single-podcast #wrapper #podcast .intro .container section ul {
              list-style: none;
              margin-left: 0;
              padding-left: 0; }
              .single-podcast #wrapper #podcast .intro .container section ul li {
                color: #664fb7;
                font-family: "Avenir-Heavy", sans-serif;
                margin-bottom: initial; }
                .single-podcast #wrapper #podcast .intro .container section ul li:before {
                  content: '→';
                  padding-right: 5px; }
          .single-podcast #wrapper #podcast .intro .container form {
            margin: 0px;
            min-width: 100%;
            min-height: 350px;
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            justify-content: center; }
          .single-podcast #wrapper #podcast .intro .container .hbspt-form {
            margin-top: 100px;
            display: flex;
            flex-direction: column;
            flex: 1;
            background: #fff;
            border: 1px solid #d9d9d9;
            box-shadow: 1px 1px 10px #ececec;
            padding: 20px;
            justify-content: center; }
            @media (max-width: 768px) {
              .single-podcast #wrapper #podcast .intro .container .hbspt-form {
                margin-top: -100px !important; } }
            .single-podcast #wrapper #podcast .intro .container .hbspt-form .form-columns-2 {
              text-align: left;
              min-width: 100%;
              min-height: 100px; }
            .single-podcast #wrapper #podcast .intro .container .hbspt-form .actions {
              margin-top: 20px; }
            .single-podcast #wrapper #podcast .intro .container .hbspt-form .hs-button.primary.large {
              background: #664fb7;
              border-color: #664fb7;
              color: #ffffff;
              min-height: 70px;
              font-family: "Avenir-Roman", sans-serif;
              border: 3px solid transparent;
              border-radius: 3px;
              outline: 0;
              text-transform: uppercase;
              letter-spacing: 0.075em;
              padding: 1.2rem;
              margin: 0 0 20px;
              position: relative;
              text-align: center;
              z-index: 1;
              transition: all 0.3s;
              line-height: 1.5;
              min-width: 228px;
              text-decoration: none;
              padding-left: 5px;
              padding-right: 5px;
              width: -webkit-max-content;
              width: -moz-max-content;
              width: max-content; }
              .single-podcast #wrapper #podcast .intro .container .hbspt-form .hs-button.primary.large:hover {
                background-color: #5dc1c3; }
            .single-podcast #wrapper #podcast .intro .container .hbspt-form label {
              line-height: 20px;
              font-size: 13px;
              font-family: "Avenir-Roman", sans-serif;
              text-transform: capitalize; }
            .single-podcast #wrapper #podcast .intro .container .hbspt-form .input {
              margin-right: 0px !important; }
            .single-podcast #wrapper #podcast .intro .container .hbspt-form input {
              box-sizing: border-box;
              min-height: 27px;
              background-color: #f5f8fa;
              display: inline-block;
              width: 95%;
              max-width: 500px;
              height: 40px;
              padding: 9px 10px;
              font-family: "Brown-light", helvetica, Arial, sans-serif;
              font-size: 13;
              line-height: 22px;
              color: #664fb7;
              border: 1px solid #cbd6e2;
              border-radius: 3px;
              margin-right: 0px; }
      .single-podcast #wrapper #podcast .explain {
        display: flex;
        min-width: 100%;
        background: #664fb7;
        padding-top: 30px;
        padding-bottom: 30px; }
        .single-podcast #wrapper #podcast .explain .container {
          color: white;
          font-size: 18px;
          text-align: center; }
          .single-podcast #wrapper #podcast .explain .container .content {
            max-width: 80%;
            display: flex;
            margin: auto;
            position: relative;
            margin-top: 50px; }
            .single-podcast #wrapper #podcast .explain .container .content:before {
              min-width: 100%;
