              color: #b0b0ff;
              position: absolute;
              top: -20px;
              left: 0;
              content: url("../../inc/images/quote.svg");
              font-family: "PlayfairDisplay", Helvetica, Arial, sans-serif; }
          .single-podcast #wrapper #podcast .explain .container .person {
